.titulo {
font-family: 'Montserrat', sans-serif;
font-size: 70px;
margin-bottom: 0;
margin-top: 70px;
}

.titulo span {
text-decoration: underline;
}

.title-a {
font-size: 24px;
margin-top: 0;
}

.title-b {
font-size: 50px;
margin-bottom: 20px;
}

.guitarras {
color: #1F313C;
margin-bottom: 60px;
}

.guitarras h2 {
font-family: 'Montserrat', sans-serif;
font-size: 35px;
}



.portada {

color: white;
padding: 20px;
height: 500px;
}

.portada button {
margin-bottom: 70px;
}


.menu {
font-size: 24px;
display: inline-block;
position: absolute;
right: 0;
}

.menu li {
display: inline-block;
margin-left: 30px;
}

.menu li:before{
content: '∘';
font-size: 50px;
line-height: 20px;
color: white;
}

.menu a {
color: white;
text-decoration: none;
}

.logotipo {
display: inline-block;
margin-left: 0;
}


.guitarra {
margin: 10px 0px 40px;
padding: 10px 2em;
border: 1px solid #1F313C;
border-radius: 5px;
display: flex;
align-items: center;
min-height: 250px;
transition: 0.3s;
position: relative;
}

.guitarra:nth-of-type(even):hover{
  box-shadow: -10px 10px 0 black;
}

.guitarra:nth-of-type(odd):hover{
  /* box-shadow: x y % color; */
  box-shadow: 10px 10px 0 red;
}


.guitarra-name {
  font-size: 3em;
  margin: 0 0 0.2em;
  border-bottom: 2px dashed black;
}


.guitarra:nth-of-type(odd){
  justify-items: flex-end;
}

.guitarra:nth-of-type(odd) .guitarra-image{
  position: absolute;
  right: 0;
  bottom: 0;
}

.guitarra ol {
  counter-reset: features;
  padding: 0;
  list-style: none;
}

.guitarra li::before {
  counter-increment: features;
  content: counter(features) ". " ;
}

.contenedor-guitarra {

padding-left: 20px;
}

.header {
position: relative;
}


.footer {
background: #273b47;
padding: 50px 10px;
}

.contacto {
