}

.col2 .button {
margin-top: 15px;
}

.intereses label {
font-size: 12px;
}

.formulario label, .sexo, .intereses {
margin-bottom: 10px;
}


.sexo {
margin-top: 10px;
}


.tabla {
margin: 50px 0;
}


.burguer-button {
width: 40px;
border-radius: 50%;
line-height: 40px;
height: 40px;
background: #395667;
display: inline-block;
text-align: center;
cursor: pointer;
position: fixed;
left: 20px;
top: 20px;
color: white;
z-index: 3;
display: none;
}

.video-demo {
max-width: 500px;
width: 100%;
border: 5px solid #3c5b6e;
}

.video-demo-contenedor {
